import React, { useEffect } from 'react';
import { useForm } from 'react-hook-form';
import { RouteComponentProps } from 'react-router';
import { Col, Row } from 'reactstrap';
import { EditForm, EditViewProps, getSelectedItem, InputGroupInline, InputType, onlyAlphaNum, WindowComponent } from 'summer';
import useCepManager from '../../../../hooks/useCepManager';
import { CLASS_NAME, ResponsavelMenorAprendiz } from '../../../../models/dtos/mfp/responsavelMenorAprendiz';
import { ESTADO_CIVIL_ENUM_VALUES } from '../../../../models/enumerated/mfp/estadoCivilEnum';
import { NACIONALIDADE_ENUM_VALUES } from '../../../../models/enumerated/mfp/nacionalidadeEnum';
import CepBairroIS from '../../../components/ger/cepBairroIS/CepBairroIS';
import CepLocalidadeIS from '../../../components/ger/cepLocalidadeIS/CepLocalidadeIS';
import CepLogradouroIS from '../../../components/ger/cepLogradouro/CepLogradouroIS';
import CepTituloIS from '../../../components/ger/cepTituloIS/CepTituloIS';
import CepUFIS from '../../../components/ger/cepUFIS/CepUFIS';

interface ResponsavelMenorAprendizFormProps extends EditViewProps<ResponsavelMenorAprendiz> {}

const labelWidth: number = 200;

const ResponsavelMenorAprendizEditView: React.FC<ResponsavelMenorAprendizFormProps & RouteComponentProps> = props => {
  const selectedItem = getSelectedItem(props);
  const formProps = useForm<ResponsavelMenorAprendiz>({
    defaultValues: selectedItem,
    mode: 'onBlur',
  });
  const cepManager = useCepManager(formProps, {
    bairro: 'cepBairro',
    cep: true,
    localidade: 'cepLocalidade',
    nome: 'logradouro',
    titulo: 'cepTitulo',
    uf: 'cepUf',
  });

  useEffect(
    () => {
      if (props.selectedItem && props.selectedItem.id) {
        setTimeout(() => {
          // @ts-ignore
          formProps.setValue('cepFmt', { cep: props.selectedItem.cep, label: props.selectedItem.cep });
        }, 500);
      }
    }, // eslint-disable-next-line react-hooks/exhaustive-deps
    []
  );

  function beforePersist(formData: ResponsavelMenorAprendiz): void {
    // @ts-ignore
    formData.cep = formData.cep.cep;
  }

  return (
    <WindowComponent title="Manutenção de Responsável pelo Aprendiz">
      <EditForm formProps={formProps} editViewProps={props} dtoClassName={CLASS_NAME} beforePersist={beforePersist}>
        <Row>
          <Col>
            <InputGroupInline
              id="codigo"
              inputProps={true}
              label="Código"
              labelWidth={labelWidth}
              formProps={formProps}
              tooltipProps={{ message: 'Preenchimento Opcional - Codificação Automática' }}
              isPrependLabel={true}
              placeholder="Informe o código"
            />
          </Col>
          <Col />
        </Row>
        <Row>
          <Col>
            <InputGroupInline
              id="nome"
              label="Nome"
              labelWidth={labelWidth}
              formProps={{ ...formProps, validation: { required: true, maxLength: 200 } }}
              isPrependLabel={true}
              tooltipProps={{ message: 'Nome do responsável pelo aprendiz.' }}
              placeholder="Informe o nome"
            />
          </Col>
        </Row>
        <Row>
          <Col>
            <InputGroupInline
              id="nacionalidade"
              label="Nacionalidade"
              labelWidth={labelWidth}
              isPrependLabel={true}
              placeholder="Selecione a nacionalidade"
              formProps={{ ...formProps, validation: { required: true } }}
              inputDropDownProps={{
                optionsProvider: NACIONALIDADE_ENUM_VALUES,
                selectProps: { isClearable: false },
              }}
              type={InputType.DROP_DOWN}
              tooltipProps={{ message: 'Nacionalidade do responsável pelo aprendiz.' }}
            />
          </Col>
          <Col>
            <InputGroupInline
              id="estadoCivil"
              label="Estado Civil"
              labelWidth={labelWidth}
              isPrependLabel={true}
              placeholder="Selecione o Estado Civil"
              formProps={{ ...formProps, validation: { required: true } }}
              inputDropDownProps={{
                optionsProvider: ESTADO_CIVIL_ENUM_VALUES,
                selectProps: { isClearable: false },
              }}
              type={InputType.DROP_DOWN}
              tooltipProps={{ message: 'Estado civil do responsável pelo aprendiz.' }}
            />
          </Col>
        </Row>
        <Row>
          <Col>
            <InputGroupInline
              id="rg"
              label="Nº do RG"
              formProps={{ ...formProps, validation: { required: true, ...onlyAlphaNum(0, 20) } }}
              isPrependLabel={true}
              labelWidth={labelWidth}
              placeholder="Informe o nome"
              tooltipProps={{ message: 'Número da carteira de identidade do responsável pelo aprendiz.' }}
            />
          </Col>
          <Col>
            <InputGroupInline
              id="cpf"
              label="CPF"
              formProps={{ ...formProps, validation: { required: true } }}
              isPrependLabel={true}
              labelWidth={labelWidth}
              placeholder="Informe o CPF"
              tooltipProps={{ message: 'Número do CPF do responsável pelo aprendiz.' }}
              type={InputType.CPF}
            />
          </Col>
        </Row>
        <Row>
          <Col>
            <InputGroupInline
              id="cargo"
              label="Cargo"
              formProps={{ ...formProps, validation: { required: true, maxLength: 30 } }}
              isPrependLabel={true}
              labelWidth={labelWidth}
              placeholder="Informe o Cargo"
              tooltipProps={{ message: 'Cargo do responsável pelo aprendiz.' }}
            />
          </Col>
        </Row>
        <Row>
          <Col>
            <CepLogradouroIS
              formProps={{ ...formProps, validation: { required: true } }}
              label="CEP"
              placeholder="Informe o CEP"
              id="cepFmt"
              labelWidth={labelWidth}
              isPrependLabel={true}
              inputSearchProps={{ selectProps: { isMulti: false } }}
              cepManager={cepManager}
              tooltipProps={{ message: 'CEP no qual o responsável pelo aprendiz reside.' }}
            />
            <input type="hidden" name="cep" ref={formProps.register} />
          </Col>
          <Col>
            <CepTituloIS
              formProps={{ ...formProps, validation: { required: true } }}
              label="Tipo de Logradouro"
              placeholder="Informe o tipo de logradouro"
              id="cepTitulo"
              labelWidth={labelWidth}
              isPrependLabel={true}
              inputSearchProps={{ selectProps: { isMulti: false } }}
              tooltipProps={{ message: 'Tipo do Logradouro no qual o responsável pelo aprendiz reside.' }}
            />
          </Col>
        </Row>
        <Row>
          <Col>
            <InputGroupInline
              formProps={{ ...formProps, validation: { required: true, maxLength: 150 } }}
              id="logradouro"
              isPrependLabel={true}
              labelWidth={labelWidth}
              label="Logradouro"
              placeholder="Informe o logradouro."
              tooltipProps={{ message: 'Logradouro no qual o responsável pelo aprendiz reside.' }}
            />
          </Col>
        </Row>
        <Row>
          <Col>
            <InputGroupInline
              formProps={{ ...formProps, validation: { required: true, maxLength: 10 } }}
              id="numero"
              isPrependLabel={true}
              labelWidth={labelWidth}
              label="Número"
              placeholder="Informe o número."
              type={InputType.NUMBER}
              tooltipProps={{ message: 'Número do endereço no qual o responsável pelo aprendiz reside.' }}
            />
          </Col>
          <Col>
            <InputGroupInline
              formProps={{ ...formProps, validation: { required: true, maxLength: 60 } }}
              id="complemento"
              isPrependLabel={true}
              labelWidth={labelWidth}
              label="Complemento"
              placeholder="Informe o Complemento."
              tooltipProps={{ message: 'Complemento do endereço no qual o responsável pelo aprendiz reside.' }}
            />
          </Col>
        </Row>
        <Row>
          <Col>
            <CepUFIS
              formProps={{ ...formProps, validation: { required: true } }}
              id="cepUf"
              labelWidth={labelWidth}
              isPrependLabel={true}
              inputSearchProps={{ selectProps: { isMulti: false } }}
              tooltipProps={{ message: 'UF no qual o responsável pelo aprendiz reside.' }}
            />
          </Col>
          <Col>
            <CepLocalidadeIS
              formProps={{ ...formProps, validation: { required: true } }}
              id="cepLocalidade"
              labelWidth={labelWidth}
              isPrependLabel={true}
              inputSearchProps={{ selectProps: { isMulti: false } }}
              tooltipProps={{ message: 'Cidade no qual o responsável pelo aprendiz reside.' }}
            />
          </Col>
        </Row>
        <Row>
          <Col>
            <CepBairroIS
              formProps={{ ...formProps, validation: { required: true } }}
              id="cepBairro"
              labelWidth={labelWidth}
              isPrependLabel={true}
              inputSearchProps={{ selectProps: { isMulti: false } }}
              tooltipProps={{ message: 'Bairro no qual o responsável pelo aprendiz reside.' }}
            />
          </Col>
        </Row>
      </EditForm>
    </WindowComponent>
  );
};

export default ResponsavelMenorAprendizEditView;
